@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz&display=swap);
.NavMain {
  background-color: rgba(0, 9, 134, 0.774);
  color:rgb(255, 255, 255);
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
  border-bottom-right-radius: 10px ;
  border-bottom-left-radius: 10px ;
}

.NavMain .nav-log {
  margin-left: auto;
  display: flex;
  color: rgb(255, 255, 255);
}
.NavMail .nav-trip {
  margin-right: auto;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.NavMain img {
  width: 100px;
}


.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-image: url("https://thepostcalvin.com/wp-content/uploads/2016/02/map-with-pins-for-GV.jpg");
    background-size: cover;
    background-position: bottom;
    padding: 30px;
    margin: 10px 0px;
    border: 1px solid rgb(156, 156, 156)
}

.home p {
    margin: 10px;
    font-weight: bold;
    font-size: 20px;
    color: white;
    text-shadow: rgb(0, 0, 0) 2px 0 10px;;
}

.home h1 {
    font-size: 40px;
    color: #f1c232ff;
    text-shadow: #0800a3 2px 0 10px;
}
.image-line {
    display: flex;
    justify-content: center;
    text-align: center;
    color: white;
    margin: 10px 0px;
    border-radius: 10px;

}
.image-line div {
    padding: 200px 75px;
    font-size: 34px;
    font-weight: bold;
    text-shadow: rgb(0, 0, 0) 5px 5px 5px;
    background-image: url(/images/Berlin-barcelona-paris-london.JPG);
    background-size: cover;
    background-repeat: no-repeat;
}


.form {
    background-color: rgba(255, 255, 255, 0.85);
    display: flex;
    text-align: center;
    /* position: absolute; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin: 5% 10%;
    padding: 5% 0%;
    width: 80%;
}
input {
    width: 40%;
}

.backgroundFormSignIn  {
    background: url("https://dunked.cdn.speedyrails.net/assets/prod/76966/880x0_p1c4p1r26q1srqia761eoo8i8va.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 700px;
    width: auto;
}

.backgroundFormSignUp  {
    background: url("https://dunked.cdn.speedyrails.net/assets/prod/76966/880x0_p1c4p1r26q1srqia761eoo8i8va.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 900px;
    width: auto;
}

.form img {
    border-radius: 100px;
    width: 10%
}
