.one-trip .destination {
    display: flex;
    flex-direction: column;
    background-size: cover;
    background-position: bottom;
    justify-content: space-evenly;
    align-items: center;
    width: 70%;   
    height: 400px;
    margin: 5px;
    border-radius: 10px;
    border: 1px solid rgb(156, 156, 156)
}
.one-trip {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.one-tripzx h1{
    font-size: 34px;
    color: white;
    text-shadow: rgb(0, 0, 0) 5px 5px 5px;
}

.one-trip .destination p {
    text-align: center;
    margin: 60px;
    font-size: 20px;
    color: white;
    text-shadow: rgb(0, 0, 0) 3px 0px 5px;
}

.activities-card {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius:5px;
    border: 1px solid black;
    width: 100px;
    margin: 5px;
    text-align: center;
    background-color: white;
}
.activities-card p {
    padding: 2px
}

.all-activities {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px;
    margin: 5px

}
.activities-and-point {
    display: flex;
}

.dashboard-head {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    background-color: rgb(253, 218, 123);
    border: 1px solid rgba(0, 9, 134, 0.774);
}

.dashboard-head h1, .dashboard-head h3 {
    color: rgba(0, 9, 134, 0.774);
}
.dashboard-table {
    border: 1px solid rgba(0, 9, 134, 0.774);
    text-align: center;
}
.dashboard-table tr, .dashboard-table td {
    border: 1px solid black;
    padding: 3px;
    margin: 20px;
}
.tourCards {
    display: flex;
    justify-content: space-evenly;
    margin: 5px;
    padding: 3px;
    border: 1px solid black;
    background-color: whitesmoke;
    z-index: 10;
    
}

.tourCards img {
    width: 200px;
    height: 100%;
    margin: 3px
}

.tourDescription {
    width: 60%;
}

.bookTour {
    display: flex;
    flex-direction: column;
    justify-content: space-around ;
}

.display-item {
    display: flex;
    justify-content: space-evenly;
    margin: 0px 15px;
    padding: 3px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: whitesmoke;
    z-index: 10;
    position: absolute;
    width: 35%;
}

.back-button {
    margin: 10px;
    display: flex;
    z-index: 10;
    justify-content: center;
}
